.app {
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: absolute;
  width: 100%;
}

.home {
  width: 100%;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
}

.parent-button {
  margin-left: 15px;
  padding: 3px;
  background: linear-gradient(rgb(132, 150, 255), rgb(145, 145, 145)),
  linear-gradient(127deg, rgb(145, 145, 145), rgb(132, 150, 255));
}

.button {
  background: #272727 repeat-x top left; float: left;
}